import React, { Component } from "react";
import PropTypes from 'prop-types';
import { BigNumber } from 'bignumber.js';

import selftokenIconCrop from "assets/img/selftoken_transparent_crop.png";


// round to `decimals` digits
// remove .000. but .100 is kept. TODO: remove redundant zeros
function formatFromWeiBnToEtherStr(bn, decimals=4) {
  return bn.div(1e18).toFormat(decimals).replace(/\.0+$/,'');
}

class Balances extends Component {
  render() {
    const { strings, ethBalanceBN, selfBalanceBN } = this.props;

    const ethBalanceStr = formatFromWeiBnToEtherStr(ethBalanceBN, 4);
    const selfBalanceStr = formatFromWeiBnToEtherStr(selfBalanceBN, 2);

    // icon floats left and text floats right when screen width <= 767px
    // icon and text in the same line when screen width >= 768px
    return (
      <div className="row">
        <div className="col-md-6 text-center mb-2">
          <div className="h3 balance-container">
            <span className="h2 mb-0 mr-3 balance-icon-container">
              <i className="fab fa-ethereum"></i>
            </span>
            <span className="balance-text-container">{ ethBalanceStr } { strings.EN_ETH }</span>
          </div>
        </div>
        <div className="col-md-6 text-center mb-2">
          <div className="h3 balance-container">
            <span className="h2 mb-0 mr-3 balance-icon-container">
              <img
                src={selftokenIconCrop}
                alt="self-token-icon"
                style={{height: "1em"}}
              />
            </span>
            <span className="balance-text-container">{ selfBalanceStr } { strings.EN_SELF }</span>
          </div>
        </div>
      </div>
    );
  }
}

Balances.propTypes = {
  strings: PropTypes.object,
  selfBalanceBN: PropTypes.object,
  ethBalanceBN: PropTypes.object,
};

Balances.defaultProps = {
  strings: {},
  selfBalanceBN: new BigNumber(),
  ethBalanceBN: new BigNumber(),
};

export default Balances;
