import React, { Component } from "react";
import { BigNumber } from 'bignumber.js'; // not web3.utils.BN in 1.0.0-beta.34
import LocalizedStrings from 'react-localization';
import copy from 'copy-to-clipboard';
import { Switch, Route, Link } from 'react-router-dom';
// const BN = require('bn.js'); // used in web3

import SelfTokenJSON from "./contracts/SelfToken.json";
import DelegatedTransferOperatorJSON from "./contracts/DelegatedTransferOperatorV5.json";
import getWeb3 from "./utils/getWeb3";

import 'bootstrap/dist/css/bootstrap.min.css';
import "./styles/AddressInput.css";
import "./styles/NumericInput.css";
import "./App.css";

import { Spinner } from "components/Spinner.js";
import DoubleBorderTitle from "components/DoubleBorderTitle.js";
import Balances from "components/Balances.js";
import DelegatedTransferForm from "components/DelegatedTransferForm.js";
import LanguageSelector from "components/LanguageSelector.js";
import ReceiveToken from "components/ReceiveToken.js";
import PleaseOpenInDappBrowser from "components/PleaseOpenInDappBrowser.js";

import trustWalletIconSquare from "assets/img/trust_wallet_icon_square.png";

import localesData from './data/locales.json';


let strings = new LocalizedStrings(localesData);

const fmt = {
  prefix: '',
  decimalSeparator: '.',
  groupSeparator: ',',
  groupSize: 3,
  secondaryGroupSize: 0,
  fractionGroupSeparator: ' ',
  fractionGroupSize: 0,
  suffix: ''
}
BigNumber.config({ FORMAT: fmt });
BigNumber.config({ EXPONENTIAL_AT: 1e+9 });

function generateDeepLinkToTrustWallet() {
  return "https://links.trustwalletapp.com/a/key_live_lfvIpVeI9TFWxPCqwU8rZnogFqhnzs4D?&event=openURL&url=" + encodeURIComponent(window.location.href);
}

class TabButton extends Component {
  render() {
    const { className, isActive, children } = this.props;

    return (
      <div className={[
        className,
        "tab-button",
        (isActive) ? "tab-button-active" : "tab-button-inactive"
      ].join(' ')}>
        { children }
      </div>
    );
  }
}

class App extends Component {
  state = {
    web3: null,
    networkId: -1,
    selfTokenContract: null,
    accounts: [],
    accountState: {
      ethBalanceBN: new BigNumber(0),
      selfBalanceBN: new BigNumber(0),
    },
    globalState: {
      totalSupplyBN: new BigNumber(0),
    },
    isAllDataFetched: false,
    isUrlCopiedMsgShown: false,
    deepLinkToTrustWallet: generateDeepLinkToTrustWallet(),
  };

  componentDidMount = async () => {
    try {
      console.log("App this.props", this.props);

      // Get network provider and web3 instance.
      const web3 = await getWeb3();

      if (!web3) {
        this.setState({
          isAllDataFetched: true,
          deepLinkToTrustWallet: generateDeepLinkToTrustWallet(),
        });
        return;
      }

      // Get the networkId and check if on valid network.
      const networkId = await web3.eth.net.getId(); // Number
      const isValidNetwork = SelfTokenJSON.networks.hasOwnProperty(networkId); // bool
      console.log("networkId", networkId);

      this.setState({
        web3,
        networkId,
        isValidNetwork,
      });

      // If not on valid network, stop here.
      if (!isValidNetwork) {
        this.setState({
          isAllDataFetched: true,
        });
        return;
      }

      // Init contract instance.
      const selfTokenContract = new web3.eth.Contract(
        SelfTokenJSON.abi,
        SelfTokenJSON.networks[networkId].address
      );

      const delegatedTransferOperator = new web3.eth.Contract(
        DelegatedTransferOperatorJSON.abi,
        DelegatedTransferOperatorJSON.networks[networkId].address
      );

      this.setState({
        selfTokenContract,
        delegatedTransferOperator,
      }, () => {
        // fetch global state
        this.fetchGlobalState();
      });

      // Use web3 to get the user's accounts.
      const accounts = await web3.eth.getAccounts();
      console.log("accounts", accounts);


      await this.setState({
        accounts
      });

      setInterval(() => {
        this.poll();
      }, 10000);
      await this.poll();

      this.setState({
        isAllDataFetched: true,
      });
    } catch (error) {
      // Catch any errors for any of the above operations.
      // Should never go here.
      alert(
        `Failed to load web3, accounts, or contract. Check console for details.`
      );
      console.error(error);
    }
  };

  poll = async () => {
    console.log("poll");

    // this.fetchGlobalState();
    this.fetchAccountState();
  }

  fetchGlobalState = async () => {
    console.log("fetchGlobalState");

    const { selfTokenContract } = this.state;

    // If contract is not ready, return.
    if (!selfTokenContract) {
      return;
    }

    // Get total supply
    const totalSupplyStr = await selfTokenContract.methods.totalSupply().call();
    const totalSupplyBN = new BigNumber(totalSupplyStr);

    // Make a clone of globalState.
    const globalState = Object.assign({}, this.state.globalState);
    globalState.totalSupplyBN = totalSupplyBN

    // Update state with the result.
    this.setState({ globalState });
  };

  fetchAccountState = async () => {
    console.log("fetchAccountState");

    const { web3, accounts, selfTokenContract } = this.state;

    // If there are no accounts injected, return.
    if (accounts.length === 0) {
      return;
    }

    // promises
    const getEthBalance = web3.eth.getBalance(accounts[0]);
    const getSelfBalance = selfTokenContract.methods.balanceOf(accounts[0]).call();
    const values = await Promise.all([getEthBalance, getSelfBalance]);
    const ethBalanceStr = values[0];
    const selfBalanceStr = values[1];
    console.log("ethBalanceStr", ethBalanceStr);
    console.log("selfBalanceStr", selfBalanceStr);


    // Make a clone of accountState.
    let accountState = Object.assign({}, this.state.accountState);
    accountState.ethBalanceBN = new BigNumber(ethBalanceStr);
    accountState.selfBalanceBN = new BigNumber(selfBalanceStr);

    // Update state with the result.
    this.setState({ accountState });
  };

  setLangCode = (langCode) => {
    strings.setLanguage(langCode);
    this.setState({});
  };

  copyUrl = () => {
    copy(window.location.href);

    // show "copied" message
    this.setState({
      isUrlCopiedMsgShown: true,
    })

    // hide "copied" message after a few seconds
    setTimeout(() => {
      this.setState({
        isUrlCopiedMsgShown: false,
      });
    }, 2000);
  };

  render() {
    const { web3, isValidNetwork, accounts, delegatedTransferOperator, networkId, isUrlCopiedMsgShown, isAllDataFetched, deepLinkToTrustWallet } = this.state;
    const { selfBalanceBN, ethBalanceBN } = this.state.accountState;

    const content = ((!isAllDataFetched) ?
      <Spinner/> :
      <div>
        {/* navbar */}
        <div className="app-navbar mb-3 clearfix">
          <div className="float-left">
            <div className="app-title">SELF DAPP</div>
          </div>
          <div className="float-right">
            <LanguageSelector strings={strings} setLangCode={this.setLangCode} />
          </div>
        </div>

        <div className="container">
          {/* warning messages */}
          <div>
            {/* No web3 */}
            { !web3 &&
              <div className="alert alert-danger">
                <PleaseOpenInDappBrowser
                  strings={ strings }
                />
                <div className="mb-1">
                  <span>1. </span>
                  <span>{ strings.msg_if_trust_wallet_installed }</span>
                  <div className="text-center">
                    <a href={ deepLinkToTrustWallet } target="_blank" rel="noopener noreferrer">
                      <button className="solid-rectangle-button bg-trust-wallet-blue">
                        <span className="mr-2">
                          <img
                            src={ trustWalletIconSquare }
                            alt="trust-wallet-icon"
                            className="trust-wallet-icon"
                          />
                        </span>
                        <span>{ strings.msg_open_in_trust_wallet }</span>
                      </button>
                    </a>
                  </div>
                </div>
                <div>
                  <span>2. </span>
                  <span>{ strings.msg_if_other_dapp_browser }</span>
                  <div className="text-center">
                    <button onClick={ this.copyUrl } className="solid-rectangle-button bg-golden">
                      <span>{ strings.msg_copy_url }</span>
                    </button>
                    { isUrlCopiedMsgShown &&
                      <span className="copied-msg">{ strings.msg_copied }</span>
                    }
                  </div>
                  <span>{ strings.msg_and_paste_in_other_dapp_browser }</span>
                </div>
              </div>
            }

            {/* Not on valid network */}
            { !!web3 && !isValidNetwork &&
              <div className="alert alert-danger">
                <div>{ strings.msg_invalid_network }</div>
                <div>{ strings.msg_please_switch_to_mainnet }</div>
              </div>
            }

            {/* Show warning message if the user or the developer is on testnet. */}
            {(isValidNetwork && networkId !== 1) && (<div>
              <div className="alert alert-warning">
                <div>{ strings.msg_on_testnet }</div>
                <div>{ strings.msg_please_switch_to_mainnet }</div>
              </div>
            </div>)}
          </div>

          <div className="mb-5">
            <DoubleBorderTitle>{ strings.Balances }</DoubleBorderTitle>
            <div className="narrow-container">
              <Balances
                strings={strings}
                selfBalanceBN={ selfBalanceBN }
                ethBalanceBN={ ethBalanceBN }
              />
            </div>
          </div>

          <div className="tab-list mb-5">
            <Link to="/transfer">
              <Switch>
                <Route exact path="/receive" render={ props => (
                  <TabButton className="w-50">
                    { strings.Transfer_SELF }
                  </TabButton>
                )} />
                <Route render={ props => (
                  <TabButton className="w-50" isActive>
                    { strings.Transfer_SELF }
                  </TabButton>
                )} />
              </Switch>
            </Link>
            <Link to="/receive">
              <Switch>
                <Route exact path="/receive" render={ props => (
                  <TabButton className="w-50" isActive>
                    { strings.Receive_SELF }
                  </TabButton>
                )} />
                <Route render={ props => (
                  <TabButton className="w-50">
                    { strings.Receive_SELF }
                  </TabButton>
                )} />
              </Switch>
            </Link>
          </div>

          <Switch>
            {/* path matches "/receive" */}
            <Route exact path="/receive" render={ props => (
              <div className="narrow-container mb-3" key="receive_self">
                {/* <DoubleBorderTitle>{ strings.Receive_SELF }</DoubleBorderTitle> */}
                <ReceiveToken
                  accounts={accounts}
                  strings={strings}
                />
              </div>
            )} />
            {/* path doesn't match "/receive" */}
            <Route render={ props => (
              <div className="narrow-container mb-3" key="transfer_self">
                {/* <DoubleBorderTitle>{ strings.Transfer_SELF_without_ETH }</DoubleBorderTitle> */}
                <DelegatedTransferForm
                  web3={web3}
                  accounts={accounts}
                  delegatedTransferOperator={delegatedTransferOperator}
                  networkId={networkId}
                  strings={strings}
                  selfBalanceBN={ selfBalanceBN }
                  callback={this.fetchAccountState}
                />
              </div>
            )} />
          </Switch>

        </div>
      </div>
    );

    return (
      <div className="App">
        <div className="bg">
          <div className="dot"></div>
          <div className="line"></div>
        </div>
        { content }
      </div>
    );
  }
}

export default App;
