import React, { Component } from "react";
import PropTypes from 'prop-types';
import { BigNumber } from 'bignumber.js'; // not web3.utils.BN
import NumericInput from 'react-numeric-input';
import ethUtil from 'ethereumjs-util';

import sig2vrs from "utils/sig2vrs";
import api from "utils/api";
import getParameterByName from "utils/getParameterByName";
import hexToEvenLengthHex from "utils/hexToEvenLengthHex";
import PleaseOpenInDappBrowser from "components/PleaseOpenInDappBrowser.js";
import { SpinnerCover } from "components/Spinner.js";

// import styles from "../styles/DelegatedTransferForm.module.css";

const fmt = {
  prefix: '',
  decimalSeparator: '.',
  groupSeparator: ',',
  groupSize: 3,
  secondaryGroupSize: 0,
  fractionGroupSeparator: ' ',
  fractionGroupSize: 0,
  suffix: ''
}
BigNumber.config({ FORMAT: fmt });
BigNumber.config({ EXPONENTIAL_AT: 1e+9 });

const hexStrToBuffer = (hexStr) => {
  return Buffer.from(ethUtil.stripHexPrefix(hexStr), 'hex');
};

const bufferToHexStr = (buffer) => {
  return '0x' + buffer.toString('hex');
};

const isSameAddress = (address1, address2) => {
  return address1.toLowerCase() === address2.toLowerCase();
};

class DelegatedTransferForm extends Component {
  state = {
    delegate: "0x548574d699FB59bdA110Cc25d9Eaf59D3Ae80E0e", // "0x0000000000000000000000000000000000000000" if no specific delegate
    recipient: "",
    amount: 100, // SELF
    fee: new BigNumber(0),
    orderId: null,
    isUsingLocalServer: false, // only works on a client hosted by localhost
    server_wallet_address_api_url: "https://api.selftoken.co/v1/getServerWalletAddress",
    delegatedTransfer_api_url: "https://api.selftoken.co/v1/delegatedTransfer",
    // local_delegatedTransfer_api_url: "http://localhost:8080/v1/delegatedTransfer",
    local_delegatedTransfer_api_url: "https://testapi.selftoken.co/v1/delegatedTransfer",
    isShowingSpinner: false,
  };

  componentDidMount = async () => {
    const { web3 } = this.props;

    if (!web3) {
      return;
    }

    const query_to = getParameterByName("to");
    const query_self = getParameterByName("self");
    // const query_amount = getParameterByName("amount");

    if (!!query_to) {
      if (web3.utils.isAddress(query_to)) {
        console.log("DelegatedTransferForm: query_to correctly parsed");
        this.setState({
          recipient: query_to,
        })
      } else {
        console.warn("DelegatedTransferForm: query_to is provided but not an address");
      }
    }

    if (!!query_self) {
      const amount = parseFloat(query_self);
      if (!isNaN(amount)) {
        console.log("DelegatedTransferForm: self amount parsed, get", amount);
        this.setState({
          amount: amount,
        });
      } else {
        console.warn("DelegatedTransferForm: query_self is provided but parseFloat returns NaN.");
      }
    }

    // now only work on localhost
    if (window.location.hostname === "localhost" || window.location.hostname === "127.0.0.1") {
      if (this.state.isUsingLocalServer) {
        await this.setState({
          delegatedTransfer_api_url: this.state.local_delegatedTransfer_api_url,
        });
      }
    }
    console.log("this.state.delegatedTransfer_api_url", this.state.delegatedTransfer_api_url);

    // get delegate address (= server wallet address)
    api(this.state.server_wallet_address_api_url, {}, (data) => {
      // if transaction is broadcasted
      // `data` is `order`
      console.log(this.state.server_wallet_address_api_url, "data", data); // address string
      this.setState({
        delegate: data,
      });
    }, (error) => {
      // if failed
      console.error(this.state.server_wallet_address_api_url, "error", error);
    });

  }

  handleAmountChange = (value) => {
    this.setState({
      amount: value,
    });
  }

  handleRecipientChange = (event) => {
    this.setState({
      recipient: event.target.value.trim(),
    });
  }

  handleOrderIdChange = (value) => {
    this.setState({
      orderId: value,
    });
  }

  handleDelegatedTransferFormSubmit = async (event) => {
    event.preventDefault();

    console.log("==== handleDelegatedTransferFormSubmit ====");

    const {
      web3,
      accounts,
      delegatedTransferOperator,
      networkId,
      strings,
      selfBalanceBN,
      callback
    } = this.props;

    const {
      recipient,
      amount, // number
      delegate,
      fee,
      delegatedTransfer_api_url,
      orderId,
    } = this.state;

    if (accounts.length === 0) {
      alert(strings.msg_no_account + "\n" + strings.msg_please_unlock);
      return;
    }

    let amountBN = new BigNumber(amount);
    let amountWeiBN = amountBN.times(new BigNumber(1e18));

    console.log("recipient", recipient);
    console.log("amount", amount, typeof amount);
    console.log("amountWeiBN", amountWeiBN.toString());

    // check address
    if (!web3.utils.isAddress(recipient)) {
      alert(strings.msg_invalid_recipient_address);
      return;
    }
    if (recipient === "0x0000000000000000000000000000000000000000") {
      alert(strings.msg_recipient_can_not_be_zero);
      return;
    }

    // check amount
    if (amountWeiBN.plus(fee).isGreaterThan(selfBalanceBN)) {
      alert(strings.msg_not_enough_self);
      return;
    }
    if (amountWeiBN.eq(0)) {
      alert(strings.msg_self_amount_can_not_be_zero);
      return;
    }

    const _operator = delegatedTransferOperator._address;
    const _to = recipient;
    const _delegate = delegate;
    const _value = amountWeiBN.toString();
    const _fee = fee.toString();
    const _nonce = (Math.floor(Date.now() / 1000)).toString();
    const _userDataNumber = orderId;
    const _userData = (
      (typeof _userDataNumber === "number") ?
      hexToEvenLengthHex(web3.utils.numberToHex(_userDataNumber)) :
      "0x"
    );
    console.log("_operator", _operator);
    console.log("_to", _to);
    console.log("_delegate", _delegate);
    console.log("_value", _value);
    console.log("_fee", _fee);
    console.log("_nonce", _nonce);
    console.log("_userData", _userData);

    // delegationHash is a 32-byte ("0x" + 64 chars) hex string.
    const delegationHash = web3.utils.soliditySha3(
      {t: "address", v: _operator},
      {t: "address", v: _to},
      {t: "address", v: _delegate},
      {t: "uint256", v: _value},
      {t: "uint256", v: _fee},
      {t: "uint256", v: _nonce},
      {t: "bytes", v: _userData},
    );
    console.log("delegationHash", delegationHash);

    // delegationSignature is a 65-byte ("0x" + 130 chars) hex string.
    const _signature = await web3.eth.sign(delegationHash, accounts[0]);
    const _vrs = sig2vrs(_signature);
    const _int_v = parseInt(_vrs.v);
    console.log("_signature", _signature);
    console.log("_vrs", _vrs);

    if (_int_v < 27) {
      _vrs.v = web3.utils.toHex(_int_v + 27);
    }
    console.log("_vrs.v", _vrs.v);

    if (_int_v !== 27 && _int_v !== 28) {
      throw new Error("_vrs.v not 27 or 28");
    }

    const prefixedDelegationHash = web3.utils.soliditySha3(
      { t: "string", v: "\x19Ethereum Signed Message:\n32" },
      { t: "bytes32", v: delegationHash },
    );

    const recoveredAddressFromNotPrefixedHash = bufferToHexStr(
      ethUtil.pubToAddress(
        ethUtil.ecrecover(
          hexStrToBuffer(delegationHash),
          parseInt(_vrs.v),
          _vrs.r,
          _vrs.s,
        )
      )
    );
    const recoveredAddressFromPrefixedHash = bufferToHexStr(
      ethUtil.pubToAddress(
        ethUtil.ecrecover(
          hexStrToBuffer(prefixedDelegationHash),
          parseInt(_vrs.v),
          _vrs.r,
          _vrs.s,
        )
      )
    );
    console.log("recoveredAddressFromNotPrefixedHash", recoveredAddressFromNotPrefixedHash);
    console.log("recoveredAddressFromPrefixedHash", recoveredAddressFromPrefixedHash);

    const isRecoveredWithoutPrefix = isSameAddress(recoveredAddressFromNotPrefixedHash, accounts[0]);
    const isRecoveredWithPrefix = isSameAddress(recoveredAddressFromPrefixedHash, accounts[0]);

    const isCorrectlyRecovered = isRecoveredWithoutPrefix || isRecoveredWithPrefix;
    console.log("isCorrectlyRecovered", isCorrectlyRecovered);

    if (!isCorrectlyRecovered) {
      throw new Error("address is not correctly recovered from the signature");
    }

    const _signedWithPrefix = isRecoveredWithPrefix;
    console.log("_signedWithPrefix", _signedWithPrefix);


    const tx_data = delegatedTransferOperator.methods.transferPreSigned(
      _to,
      _delegate,
      _value,
      _fee,
      _nonce,
      _userData,
      _signedWithPrefix,
      // _signature
      _vrs.r,
      _vrs.s,
      _vrs.v
    // );
    ).encodeABI();

    const params = {
      _to,
      _delegate,
      _value,
      _fee,
      _nonce,
      _userData,
      _signedWithPrefix,
      _sig_r: _vrs.r,
      _sig_s: _vrs.s,
      _sig_v: _vrs.v,
    };
    const transfer_from = accounts[0];

    const post_data = {
      networkId,
      tx_data,
      params,
      transfer_from,
    }
    console.log("post_data", post_data);

    this.setState({
      isShowingSpinner: true,
    });
    api(delegatedTransfer_api_url, post_data, (order) => {
      // if transaction is broadcasted
      // `data` is `order`
      console.log("order", order);
      this.setState({
        isShowingSpinner: false,
      });
      alert(
        strings.msg_transaction_submitted + "\n" +
        strings.Transaction_Number + ": " + order._id
      );
      callback();
    }, (error) => {
      // if failed
      console.error("error", error);
      this.setState({
        isShowingSpinner: false,
      });
      alert(
        strings.msg_tx_failed + strings.msg_possible_cause + "\n" +
        "1. " + strings.msg_not_enough_self + "\n" +
        "2. " + strings.msg_error_in_server + "\n" +
        strings.msg_please_contact_us_and_let_us_help_you
      );
    });
  }

  render() {
    const { web3, strings, selfBalanceBN } = this.props;
    const { amount, fee, recipient, isShowingSpinner } = this.state;

    if (!web3) {
      return <div>
        <div className="narrow-container mb-5">
          <div className="alert alert-danger">
            <PleaseOpenInDappBrowser
              strings={ strings }
            />
          </div>
        </div>
      </div>;
    }

    const amountWeiBN = (new BigNumber(amount)).times(new BigNumber(1e18));
    const isTokenNotEnough = amountWeiBN.plus(fee).isGreaterThan(selfBalanceBN);

    const isAddressInvalid = !web3.utils.isAddress(recipient);

    return (<div>
      {
        isShowingSpinner &&
        <SpinnerCover/>
      }
      <div className="text-center">
        <form
          onSubmit={this.handleDelegatedTransferFormSubmit}
          id="transfer-form"
          className="transfer-form"
        >
          <div className="form-group">
            <label htmlFor="delegatedTransferRecipientInput">
              { strings.Recipient }
            </label>

            <input
              type="text"
              className={
                "address-input address-text" +
                ( isAddressInvalid ? " text-danger" : "" )
              }
              id="delegatedTransferRecipientInput"
              value={this.state.recipient}
              onChange={this.handleRecipientChange}
              placeholder="0x0000000000000000000000000000000000000000"
            />
          </div>

          <div className="form-group">
            <div className="text-center mb-3">
              <label htmlFor="transferAmount">{ strings.Amount } ({ strings.EN_SELF })</label>
              <NumericInput
                id="transferAmount"
                type="number"
                pattern="[\d\.]*"
                mobile
                className={
                  "numeric-input" + (isTokenNotEnough?" text-danger":"")
                }
                min={0}
                step={10}
                value={this.state.amount}
                onChange={this.handleAmountChange}
              />
            </div>
          </div>
          {/*
          <div className="form-group">
            <div className="text-center mb-3">
              <label htmlFor="orderId">Order ID</label>
              <NumericInput
                id="orderId"
                mobile
                className="numeric-input"
                min={0}
                step={1}
                value={this.state.orderId}
                onChange={this.handleOrderIdChange}
              />
            </div>
          </div> */}

          <div className="text-center">
            <button type="submit" className="solid-rectangle-button bg-golden">
              <span>{ strings.Transfer }</span>
            </button>
          </div>
        </form>
      </div>
    </div>);
  }
}

DelegatedTransferForm.propTypes = {
  web3: PropTypes.object,
  accounts: PropTypes.array,
  delegatedTransferOperator: PropTypes.object,
  networkId: PropTypes.number,
  strings: PropTypes.object,
  selfBalanceBN: PropTypes.object,
  callback: PropTypes.func,
};

DelegatedTransferForm.defaultProps = {
  web3: {},
  accounts: [],
  delegatedTransferOperator: {},
  networkId: 1,
  strings: {},
  selfBalanceBN: new BigNumber(),
  callback: () => {},
};

export default DelegatedTransferForm;
