
const hexToEvenLengthHex = (hexStr) => {
  if (typeof hexStr !== 'string') {
    throw new Error("Error in hexToEvenLengthHex, hexStr must be type 'string', is currently type " + (typeof hexStr) + ".");
  }

  const stripped = (hexStr.slice(0, 2) === '0x') ? hexStr.slice(2) : hexStr;
  return (stripped.length % 2 === 0) ? ('0x' + stripped) : ('0x0' + stripped);
};

export default hexToEvenLengthHex;
